import React, { useContext } from "react";
import { Link } from "react-router-dom";
import { FetchDataContext } from "../../context/FetchDataProvider";
import "./RestaurantScreen.css";

const RestaurantScreen = ({ match }) => {
  const restaurants = useContext(FetchDataContext);
  if (!restaurants) {
    return null;
  }
  const restaurant = restaurants.filter((res) => {
    return res.id === match.params.id;
  });
  return (
    <div className="restaurant-container">
      <div>
        {restaurant.map((res) => {
          return (
            <div className="restaurantScreen" key={res.id}>
              <div
                className="img-container"
                style={{
                  backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),url(${
                    require(`../../imgs/${res.id}.jpg`).default
                  })`,
                }}
              >
                <div className="back-to">Back to restaurants</div>
                <div className="txt">
                  <h1 className="res-name">{res.name}</h1>
                  <h2 className="res-cuisine">{res.cuisine} Restaurant</h2>
                </div>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default RestaurantScreen;
